<style>
    .bg-card{
        font-size: 20px;
        font-weight: 900;
        background-image: url("../../assets/bg-card-footer.jpg");
        background-size: cover;
        background-position:center;
        background-repeat: no-repeat;
        height: 100vh;
    }
</style>
<template v-slot:activator="{ on }">
    <div class="bg-card">
        <v-container>
            <v-card class="mx-auto">
                <v-card-title style="text-align: center;">ĐĂNG NHẬP</v-card-title>
                <v-card-actions class="justify-center">
                    <v-col style="text-align: center;">
                        <v-text-field variant="outlined" label="Tên đăng nhập"></v-text-field>
            
                        <v-text-field 
                            variant="outlined" 
                            label="Mật khẩu"
                            v-on="on"
                            :type="isShowPass ? 'text':'password'"
                        >
                            <template v-slot:append-inner>
                                <v-icon @click="XemPassword">{{ isShowPass ? "mdi-eye-off" : "mdi-eye" }}</v-icon>
                            </template>
                        </v-text-field>
                        <v-btn style="background-color: #28a745;">ĐĂNG NHẬP</v-btn>
                    </v-col>
                </v-card-actions>
            </v-card>
        </v-container>
        <Footer></Footer>
    </div>
    
</template>
<script>
import Footer from './layout/Footer.vue';

    export default{
        data(){
            return{
                isShowPass:false
            }
        },
        methods:{
            XemPassword(){
                this.isShowPass = !this.isShowPass
            }
        },
        components:{
            Footer
        }
    }
</script>
